.slider {
  padding: 35px 10px 70px;

  @media (min-width: $screen-l) {
    width: 1280px;
    margin: 0 auto;
  }

  &__item {
    position: relative;
    height: 380px;

    @media (min-width: $screen-m) {
      height: 350px;
    }

    @media (min-width: $screen-l) {
      height: 500px;
    }

    &__inner {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      width: 100%;

      @media (min-width: $screen-l) {
        width: 600px;
      }
    }

    &-about {
      font-style: italic;
      padding: 0 20px;
      max-width: 450px;

      @media (min-width: $screen-l) {
        padding: 0 40px 30px;
      }
    }

    &-text {
      // position: relative;
      position: absolute;
      bottom: 5px;
      padding: 30px 10px 30px;
      height: 280px;
      width: 100%;
      border: 8px solid transparent;

      &:before {
        content: '';
        display: block;
        position: absolute;
        top: -8px;
        bottom: -8px;
        left: -8px;
        right: -8px;
        border: 8px solid $gray-lighten;
      }

      @media (min-width: $screen-m) {
        padding: 40px 30px 50px;
        height: 260px;
      }

      @media (min-width: $screen-l) {
        padding: 40px 30px 40px;
        width: 600px;
        min-height: 355px;
        bottom: 50px;
      }
    }

    &-descr {
      margin: 0;
      padding-bottom: 40px;

      @media (min-width: $screen-l) {
        padding-bottom: 30px;
        font-size: 40px;
        line-height: 1.3;
      }
    }

    &-link {
      position: absolute;
      bottom: 25px;
      font-size: 16px;
      color: $link-color;

      &:hover {
        color: $link-color-hover;
      }
    }

    &-img {
      position: absolute;
      right: 50px;
      top: 20px;
      bottom: 0;
      width: auto;
      height: 450px;

      &__link {
        display: none;
        // margin-top: 50px;
        // margin-left: 70px;
        // margin-right: 70px;
      }
    }

    &-img__link {

      @media (min-width: $screen-l) {
        display: inline-block;
      }
    }
  }

  &__item-text:before {

    @media (min-width: $screen-l) {
      right: 90px;
      z-index: -1;
    }
  }

  @media (min-width: $screen-m) {
    padding: 35px 140px 0;
  }

  @media (min-width: $screen-l) {
    padding: 35px 120px 0;
  }
}

.slick-arrow {
  position: absolute;
  width: 40px;
  height: 40px;
  font-size: 0;
  border: none;
  background-color: $second-color-light;
  border-radius: 50%;
  cursor: pointer;

  &:hover {
    background-color: $second-color;
  }

  @media (min-width: $screen-l) {
    top: 50%;
  }
}

.slick-prev {
  bottom: 0;
  left: 33%;

  @media (min-width: $screen-m) {
    top: 58%;
    left: 50px;
  }

  @media (min-width: $screen-l) {
    top: 50%;
    left: 50px;
  }

  &:before {
    position: absolute;
    content: '';
    display: inline-block;
    top:  12px;
    bottom: 0;
    left: 14px;
    right: 0;
    background-image: url(./../img/book-store/sprites/arrow-slider.png);
    background-repeat: no-repeat;
    transform: rotate(0);
  }

  &:hover:before {
    top: 0;
    bottom: 12px;
    left: 0;
    right: 17px;
    background-image: url(./../img/book-store/sprites/arrow-slider--hover.png);
    background-repeat: no-repeat;
    transform: rotate(180deg);
  }
}

.slick-next {
  bottom: 0;
  right: 33%;

  @media (min-width: $screen-m) {
    top: 58%;
    right: 50px;
  }

  @media (min-width: $screen-l) {
    top: 50%;
    right: 50px;
  }

  &:before {
    position: absolute;
    content: '';
    display: inline-block;
    top: 0;
    bottom: 12px;
    left: 0;
    right: 14px;
    background-image: url(./../img/book-store/sprites/arrow-slider.png);
    background-repeat: no-repeat;
    transform: rotate(180deg);
  }

  &:hover:before {
    top: 12px;
    bottom: 0;
    left: 17px;
    right: 0;
    background-image: url(./../img/book-store/sprites/arrow-slider--hover.png);
    background-repeat: no-repeat;
    transform: rotate(0);
  }
}

